import React, { useState } from "react";
import { auth, database, googleProvider, microsoftProvider } from "../firebaseConfig";
import { signInWithPopup } from "firebase/auth";
import { useNavigate } from 'react-router-dom';
import Loading from "./Loading";
import googleLogo from '../google1Logo.svg';
import microsoftLogo from '../comp/Microsoft.png'; 
import './main.css';
import logo from './Logo.png';

const Login = () => {
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  const signInWithGoogle = () => {
    setIsLoading(true);
    signInWithPopup(auth, googleProvider)
      .then((result) => {
        console.log(result);
        navigate('/user');
      })
      .catch((error) => {
        console.log(error);
        setIsLoading(false);
      });
  };

  const signInWithMicrosoft = () => {
    setIsLoading(true);
    signInWithPopup(auth, microsoftProvider)
      .then((result) => {
        console.log(result);
        navigate('/user');
      })
      .catch((error) => {
        console.log(error);
        setIsLoading(false);
      });
  };

  if (isLoading) {
    return <Loading />;
  }

  return (
    <div>
      <div className="titel-container"></div>
      <div className="logo-container">
        <img src={logo} alt="logo" className="logo" />
      </div>

      <div className="container-login">
        <div className="main-eror">
          <div className="erorScreen">
            <h2>     <br/><br/>  </h2>
            <button className="signin" onClick={signInWithGoogle}>
              <img src={googleLogo} className="googleLogo" alt="Google Logo" />Sign in with Google
            </button>
            <button className="signinM" onClick={signInWithMicrosoft}>
              <img src={microsoftLogo} className="microsoftLogo" alt="Microsoft Logo" />Sign in with Microsoft
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
